.nav__toggle {
  top: -43px;
  right: 21px;
  position: absolute;
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 0;
  width: 24px;
  height: 18px;
  border: none;
  outline: none;
  background-color: transparent;
}
@media (min-width: 768px) {
  .nav__toggle {
    display: none;
  }
}
.nav__toggle:hover {
  cursor: pointer;
  opacity: 0.6;
}
.nav__toggle:active {
  cursor: pointer;
  opacity: 0.3;
}

.nav__toggle-inner {
  position: relative;
  display: inline-block;
  vertical-align: bottom;
  width: 24px;
  height: 2px;
  background-color: #222222;
  transform-origin: left center;
  transition: background-color 0.5s ease;
}
.nav__toggle-inner::before, .nav__toggle-inner::after {
  content: "";
  position: absolute;
  display: block;
  left: 0;
  width: 100%;
  height: 2px;
  background-color: #222222;
  transform-origin: left center;
  transition: transform 0.5s ease;
}
.nav__toggle-inner::before {
  top: -8px;
}
.nav__toggle-inner::after {
  top: 8px;
}

@media (max-width: 767px) {
  .nav--nojs .nav__toggle {
    display: none;
  }
  .nav--opened .nav__toggle-inner {
    background-color: transparent;
  }
  .nav--opened .nav__toggle-inner::before {
    width: 25px;
    transform: rotate(45deg) translate(5px, -5px);
  }
  .nav--opened .nav__toggle-inner::after {
    width: 25px;
    transform: rotate(-45deg) translate(4px, 6px);
  }
  .nav--opened .nav__wrapper {
    position: absolute;
    top: 0;
    right: 0;
    left: 0;
    z-index: 10;
    background-color: #ffffff;
  }
  .nav--closed .nav__wrapper {
    display: none;
  }
  .nav--nojs .nav__wrapper {
    position: static;
    display: block;
  }
}
